
import { defineComponent, ref } from 'vue'
import { useStore } from 'vuex'
import useFetching, { FetchStatus, ResponseFetching } from '@/hooks/useFetching'
import { ILengthClassForm } from '@/types'
import PageCard from '@/components/PageCard.vue'
import LengthClassForm from '@/views/system/localization/length-class/forms/LengthClassForm.vue'

export interface IData {
  model: ILengthClassForm | null;
}

export default defineComponent({
  setup () {
    const store = useStore()

    const [saveForm, saveStatus, saveError] = useFetching((dataForm: ILengthClassForm) => {
      return store.dispatch('lengthClass/createForm', dataForm)
    }) as ResponseFetching<Promise<ILengthClassForm>>

    const form = ref()

    return {
      saveForm,
      saveStatus,
      saveError,
      form
    }
  },

  components: {
    PageCard,
    LengthClassForm
  },

  computed: {
    isLoading (): boolean {
      return this.saveStatus === FetchStatus.pending
    }
  },

  data (): IData {
    return {
      model: {
        name: '',
        value: '0.00000000',
        unit: 'кг'
      }
    }
  },

  methods: {
    handleBack () {
      this.$router.push({ name: 'length-class' })
    },

    async handleSubmit () {
      if (this.form && await this.form.validate()) {
        const data: ILengthClassForm = await this.saveForm(this.model)
        if (typeof data !== 'undefined') {
          this.model = data
        }

        if (this.model?.id) {
          this.$router.push({ name: 'length-class-update', params: { id: this.model.id } })
        }
      }
    }
  }
})
